.container_ty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.spinner_ty {
  display: inline-block;
  perspective: 1000px; /* Добавляем перспективу */
}

.text_ty {
  font-size: 24px;
  font-weight: bold;
  color: #f6a617;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  animation: rotate_ty 2s linear infinite;
  transform-style: preserve-3d; /* Сохраняем 3D-структуру */
  padding: 10px; /* Добавляем отступы, чтобы текст не "слипался" с тенью */
  position: relative; /* Устанавливаем относительное позиционирование для добавления тени */
}

.text_ty::before {
  content: attr(data-content); /* Отображаем текст */
  position: absolute; /* Абсолютное позиционирование для тени */
  top: 0;
  left: 0;
  color: rgba(0, 0, 0, 0.2); /* Цвет тени */
  transform: translate(-2px, -2px); /* Смещаем тень влево и вверх */
}

@keyframes rotate_ty {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
