/* Для Webkit-браузеров (например, Chrome, Safari) */
::-webkit-scrollbar {
  width: 8px; /* Толще скроллбар */
}

::-webkit-scrollbar-track {
  background: transparent; /* Прозрачный фон скроллбара */
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2); /* Полупрозрачный скроллбар */
  border-radius: 4px; /* Скругление углов */
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.4); /* Более темный при наведении */
}

/* Для Firefox */
body {
  scrollbar-width: auto; /* Толще скроллбар */
  scrollbar-color: rgba(0, 0, 0, 0.2) transparent; /* Полупрозрачный скроллбар и прозрачный фон */
}

/* Для Webkit-браузеров (например, Chrome, Safari) */
.class::-webkit-scrollbar {
  width: 4px; /* Очень тонкий скроллбар */
}

.class::-webkit-scrollbar-track {
  background: transparent; /* Прозрачный фон скроллбара */
}

.class::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2); /* Полупрозрачный скроллбар */
  border-radius: 2px; /* Скругление углов */
}

.class::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.4); /* Более темный при наведении */
}

/* Для Firefox */
.class {
  scrollbar-width: thin; /* Очень тонкий скроллбар */
  scrollbar-color: rgba(0, 0, 0, 0.2) transparent; /* Полупрозрачный скроллбар и прозрачный фон */
}
